import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// element-ui
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import './apis' // axios
import './filters' // 

import arrowIcon from '@/components/status/ArrowIcon'

// mock
// if (process.env.NODE_ENV === 'development') {
//   require('./mock')
// }
require('./mock')

Vue.use(ElementUI, { size: 'medium' })

Vue.component('arrowIcon', arrowIcon)

Vue.config.productionTip = false

Vue.config.errorHandler = function (err, vm, info) {
  console.error('-V', err, vm, info)
}

const vm = new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

// reject
window.addEventListener('unhandledrejection', function browserRejectionHandler (event) {
  if (event) {
    event.preventDefault()
    const error = event.reason
    const isScriptError = !error.code && error.name
    if (isScriptError) {
      vm.$alert('', '', { type: 'error' })
      console.error('-C', error)
    }
  }
})
