import Mock from 'mockjs'
import { responseData } from './utils'

const Random = Mock.Random

Mock.mock('/getActivityList', 'post', options => {
  const params = typeof options.body === 'string' ? JSON.parse(options.body).body : options.body.body

  const list = new Array(params.pageSize).fill(1).map((a, idx) => {
    return {
      id: idx + 1,
      activity: Random.cword(1, 5),
      deptName: Random.pick(['CF', 'MK', 'MA']),
      principal: Random.pick(['CF1', 'MK1', 'MA1']),
      type: Random.pick(['', '']),
      belong: Random.pick(['', 'DLR']),
      mode: Random.pick(['', '']),
      plat: Random.pick(['APP', 'H5', '']),
      status: Random.pick(['00', '01', '02', '03', '10', '11', '20', '30', '90']),
      unissued: Random.pick(['', '', '', '']),
      date: new Date()
    }
  })

  return responseData(10000000, {
    list: list,
    pageNum: params.pageNum,
    pageSize: params.pageSize,
    total: 90
  }, '')
})

